export const createUniqueString = () => {
  return Date.now().toString(36) + Math.random().toString(36).substr(2);
}

export const getPropertyDivideValue = (value: string) => {
  if (!value) return { value: 0, unit: '' }
  const intValue = parseInt(value, 10)
  const unit = value.replace(`${intValue}`, '')
  return { value: intValue, unit: unit }
}

export const targetParentNodeByTagName = (currentNode: HTMLElement | SVGElement, targetSelecor: string) => {
  if (currentNode.tagName === targetSelecor) return currentNode;
  let targetNode: HTMLElement | SVGElement | string = ''

  while (currentNode.parentNode){
    if (currentNode.tagName === targetSelecor) {
      targetNode = currentNode
      break;
    }
    currentNode = currentNode.parentNode as HTMLElement;
  }
  return targetNode as HTMLElement | SVGElement
};
